<template lang="pug">
    ul
        li.subject__item-title Группы:
        li.subject__item-container(v-for="group,idx in subjectGroups") {{idx+1}}) {{group.code}}
    
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    data() {
        return {
        }
    },
    created() {
        this.fetchSubjectGroups(this.subject.id)
    },
    computed: {
        ...mapState("subjectGroups",{
            subjectGroups: state => state.subjectGroups
        })
    },
    props: {
        subject: Object
    },
    methods: {
        ...mapActions('subjectGroups', ['fetchSubjectGroups'])
    }
}
</script>
